.section_first {
  background-color: #ff8fe9;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* margin-top: 100px; */
  position: relative;
}
.panel1 {
  width: 100%;
}

.rafflebutton {
  width: 300px;
  height: 60px;
  background-color: black;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 33px 0;
}
.rafflebutton span {
  color: #d7d7ef;
  font-family: OCR-A Regular;
  font-size: 21px;
}
.section_second {
  background-color: #d7d7ef;
  display: flex;
  justify-content: center;
  align-items: center;
}
.textArea {
  width: 75%;
}
.textArea p {
  font-family: OCR-A Regular;
  font-size: 18px;
  margin: 22px 0;
  text-transform: uppercase;
}

.section_third {
  background-color: #ddd;
  width: 100%;
  /* height: 100vh; */
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.videoArea {
  width: 100%;
  /* height: 80vh; */
}

.section_fourth {
  background-color: #b3e8f0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section_fourth img {
  width: 100%;
}
.buttons {
  margin: 33px 0;
  display: flex;
  width: 65%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.buttons div {
  width: 300px;
  height: 60px;
  background-color: black;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin: 0 0 33px 0; */
}
.buttons div span {
  color: #d7d7ef;
  font-family: OCR-A Regular;
  font-size: 20px;
}
.section_fifth {
  background-color: #d7d7ef;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.section_fifth img {
  width: 100%;
}
.section_sixth {
  background-color: #5456ef;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section_sixth img {
  width: 100%;
}

@font-face {
  font-family: "OCR-A Regular";
  src: local("OCR-A Regular"), url("../../fonts/OCR-A Regular.ttf") format("truetype");
}

@media screen and (max-width: 1410px) {
  .buttons div {
    width: 230px;
    /* margin: 0 0 33px 0; */
  }
  .buttons div span {
    font-size: 15px;
  }
}
@media screen and (max-width: 1090px) {
  /* .section_first {
    padding: 20px 0;
  } */
  .rafflebutton {
    width: 220px;
    height: 40px;
  }
  .rafflebutton span {
    font-size: 15px;
  }

  .buttons {
    display: inline;
  }
  .buttons div {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
    margin: 11px 0;
    height: 40px;
  }
  .buttons div span {
    font-size: 15px;
  }
  .textArea p {
    font-size: 15px;
  }
}
@media screen and (max-width: 400px) {
  .rafflebutton {
    width: 200px;
    height: 35px;
  }
  .rafflebutton span {
    font-size: 12px;
  }

  .buttons {
    display: inline;
  }
  .buttons div {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    margin: 8px 0;
    height: 35px;
  }
  .buttons div span {
    font-size: 12px;
  }
  .textArea p {
    font-size: 12px;
  }
}
